.bar {
  display: inline-block;
  width: 100%;
  height: 0.3em;
  margin-top: 1em;
  position: relative;
  transform: rotateX(60deg);
  transform-style: preserve-3d;
}

.bar .bar-face {
  display: inline-block;
  width: 100%;
  height: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: rgba(169, 169, 169, 0.2);
  transform-origin: 50% 100%;
}

