  transform: translateZ(0.3em);
}

.bar .bar-face.front {
  transform: rotateX(-90deg);
}

.bar .bar-face.right {
  left: auto;
  right: -0.5em;
  width: 0.3em;
  transform: rotateX(-90deg) rotateY(90deg) translateX(0.5em);
}

.bar .bar-face.back {
  transform: rotateX(-90deg) rotateY(0deg) translateZ(-0.3em);
}

.bar .bar-face.left {
  width: 0.3em;
  transform: rotateX(-90deg) rotateY(-90deg) translateX(-0.5em) translateZ(0.5em);
}

